html {
  overflow-x: hidden;
}

.login-side {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.password-option {
  justify-content: space-between;
  align-items: center;
  margin-top: 0;
  display: flex;
}

.login-side input[type="email"], .login-side input[type="password"] {
  background: #eef;
  border: none;
  border-radius: 6px 0 0 6px;
  width: 300px;
  height: 40px;
}

.login-side input[type="checkbox"] {
  width: 20px;
  height: 35px;
}

#login {
  color: #fff;
  background: #059b05;
  margin-top: 25px;
}

.checker {
  align-items: center;
  gap: 1rem;
  display: flex;
}

#google {
  font-family: sans-serif;
  font-size: 13px;
  font-weight: initial;
  background: #fff;
  border: 1px solid #d3d3d3;
  width: 100%;
  margin-top: 20px;
}

.contents h1 {
  font-size: 35px;
  font-weight: 600;
}

textarea:focus, input:focus {
  outline: none;
}

#second {
  font-family: Montserrat, sans-serif;
  font-size: 30px;
}

@media screen and (width <= 825px) {
  .login-pic {
    display: none;
  }
}

@media screen and (width <= 450px) {
  body {
    padding: 0 20px;
  }

  .login-side input[type="email"], .login-side input[type="password"] {
    background: #eef;
    border: none;
    border-radius: 6px 0 0 6px;
    width: 240px;
    height: 40px;
  }
}

@media screen and (width <= 320px) {
  body {
    padding: 0 25px;
  }

  .login-side input[type="email"], .login-side input[type="password"] {
    background: #eef;
    border: none;
    border-radius: 6px 0 0 6px;
    width: 200px;
    height: 40px;
  }

  .contents h1 {
    font-size: 25px;
  }
}

